import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { makeStyles } from '@material-ui/core/styles'
import BrokenImg from './BrokenImg'

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    display: 'block',
    height: '100%',
    width: '100%'
  },
  img: {
    display: 'block',
    height: '100%',
    width: '100%'
  },
  brokenPlaceholder: {
    display: 'block',
    height: '100%',
    width: '100%'
  },
  imgBroken: {
    display: 'none'
  }
}))

export const RawImage = ({ className, getPhotoUrls, sizePriority, dataUrl, photo, imgRef, cover, style, ...props }) => {
  const classes = useStyles()
  const [imgUrls, setImgUrls] = useState()

  const [altCount, setAltCount] = useState(0)
  const [broken, setBroken] = useState(false)

  useEffect(() => {
    if (!dataUrl && photo && getPhotoUrls) {
      getPhotoUrls(photo, ['large', 'medium']).then(setImgUrls)
        .catch(() => {
          setBroken(true)
        })
    }
  }, [photo])

  // If nothing works then show a broken image component
  if (!dataUrl && (!imgUrls || imgUrls.length < altCount + 1 || broken)) {
    return (
      <BrokenImg className={classNames(className, classes.brokenPlaceholder)} loading={!broken} />
    )
  }
  else {
    return (
      <img ref={imgRef} className={classNames(className, broken ? classes.imgBroken : classes.img)}
        alt={sizePriority[altCount]}
        style={{
          ...style,
          objectFit: cover ? 'cover' : 'contain'
        }}
        onError={(err) => {
          setBroken(true)
          if (imgUrls && altCount < imgUrls.length) {
            console.log('Setting Alternate image', altCount)
            console.error(err)
            // Now that uploading works properly retries are dead
            setAltCount(altCount + 1)
          }
        }}
        // onLoad={onLoad}
        src={dataUrl || imgUrls[altCount]}
        {...props}
      />
    )
  }
}

RawImage.propTypes = {
  className: PropTypes.string,
  sizePriority: PropTypes.arrayOf(PropTypes.string),
  imgRef: PropTypes.any,
  getPhotoUrls: PropTypes.func,
  cover: PropTypes.bool,
  dataUrl: PropTypes.string,
  // onLoad: PropTypes.func,
  photo: PropTypes.object,
  style: PropTypes.object
}

export default RawImage
